  </div>
</template>

<script>

export default {
  name: 'HomeView',
  props: ['theme'],
  data() {
    return {
      todoList: this.$store.state.todoList,
      textInput: '',
      existeTarefa: false
    }
  },
  methods: {
    addTarefa() {
      if(this.textInput.trim() != ''){
      this.tarefa = {
        tarefa: this.textInput,
        completed: false
      }

      this.todoList.push(this.tarefa)
      localStorage.setItem('todoList',JSON.stringify(this.$store.state.todoList))

      this.existeTarefa = false
      this.textInput = ''
      }

    },
    deleteTarefa(todo) {
      this.index = this.todoList.indexOf(todo)
      if(this.index >= 0){
        this.todoList.splice(this.index, 1)
        localStorage.setItem('todoList',JSON.stringify(this.todoList))
      }
      if(this.todoList.length == 0) {
        this.existeTarefa = true
      }
    },
    concluirTarefa(todo) {
      this.index = this.todoList.indexOf(todo)

      this.todoList[this.index].completed = true

      localStorage.setItem('todoList',JSON.stringify(this.todoList))
    },
    completas() {
      return this.todoList.filter(todo => todo.completed)
    },
    incompletas() {
      return this.todoList.filter(todo => !todo.completed)
    }
  },
  mounted() {
    if(this.todoList.length == 0) {
      this.existeTarefa = true
    }
  },
  computed: {
    verificarExisteTarefa() {
      return this.existeTarefa
    }
  }
}

</script>

